import React from 'react';
import { connect } from 'react-redux';
import { mapStateToProps } from "./MainCalc";

class Gibs extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            infoText: true
        }
    }

    handleInfoDisplay = () => {
        this.setState(prevState => ({
            infoText: !prevState.infoText
        }))
    };

    render() {
        let podstawaChorobowego = Math.round((this.props.finalUopSalary * 0.8629 + 0.00001) * 100) / 100;
        let podstawaChorobowego2 = Math.round((podstawaChorobowego * 0.8) * 100) / 100;
        let dzienNieobecnosciPracownik = Math.round((podstawaChorobowego2 / 30) * 100) / 100;
        let dzienNieobecnosciPrzedsiebiorcy = Math.round(((((this.props.finalZusType === 111.25 ? 675 : 2859) * 0.8629) * 0.8) / 30 + 7.12) * 100) / 100;
        let podstawaUop1 = Math.round((this.props.finalUopSalary * 0.8629) *100) /100;
        let podstawaUop2 = Math.round((this.props.finalUopSalary * 0.8629) * 0.800 * 100) / 100;
        let podstawaB2B0 = this.props.finalZusType === 'maly ZUS' ? 675.00 : 2859.00;
        let podstawaB2B1 = Math.round(((this.props.finalZusType === 111.25 ? 675.00 : 2859.00) * 0.8629) *100) /100;
        let podstawaB2B2 = Math.round(((this.props.finalZusType === 111.25 ? 675.00 : 2859.00) * 0.8629 * 0.800) *100) /100;

        return (
            <div className={'gibs'} style={this.props.elStyle}>

                <h3>Porownanie zasilku chorobowego przy wlasnej dzialalnosci i umowie o prace</h3>


                <p className={'fancy-text info-text-gibs'} onClick={this.handleInfoDisplay}>
                    {this.state.infoText === true ? 'Szczegy' : "W przypadku opacania dowolnej skadki chorobowejprzedsibiorca " +
                        "ma prawo do otzymania zasiku chorobownego tak jak zatrudniony na umow o prac.Z t rnic," +
                        " e przedsibiorca paci skadki ZUS od niszej podstawy ni pracujcy na umow o prac (60% przecitnego " +
                        "wynagrodzenia w gospodarce)Po ustaleniu podstawy wynagrodzenia chorobowego albo zasiku brutto, ustala si tzw. " +
                        "podstaw netto.Podstawa netto to podstawa brutto pomniejszona o 13,71% tej podstawy brutto, a 13,71% to suma skadek" +
                        " na ubezpieczenia spoeczne,finansowana przez pracownika (odpowiednio 9,76% ubezpieczenie emerytalne, 1,5% ubezpieczenie rentowe" +
                        " i 2,45% ubezpieczenie chorobowe).Majc ju ustalon podstaw netto, dzielimy j przez 30 (zawsze przez 30, niezalenie od tego," +
                        " ile dany miesic ma dni),mnoymy razy 80% (albo 70% za pobyt zasikowy w szpitalu czy 100%, jeli jest to zasiek macierzyski albo " +
                        "niezdolno do pracy, przypadajca w okresie ciy)."}
                </p>
                <h3>Poniej przedstawiamy wyliczenia</h3>


                <div className={'compare-holder'}>
                    <div className={'compare-holder-uop'}>

                        <h3>Umowa o prac</h3>
                        <div className={'basic-holder'}>
                            <p className={'result-text'}>Podstawa</p>
                            <p className={'result-text basic-value-display'}><span className={'money-display'}>{Math.round((this.props.finalUopSalary) *100) /100}z</span></p>
                        </div>


                        <div className={'basic-holder-line'}>
                            <p className={'result-text '}><span className={'money-display'}>{podstawaUop1}z</span> {'\x3C'} -- minus <span className={'percentage-display'}>13,71%</span></p>
                        </div>

                        <div className={'basic-holder-line'}>
                            <p className={'result-text'}><span className={'money-display'}>{podstawaUop2}z</span> {'\x3C'}-- z tego <span className={'percentage-display'}>80%</span> (<span className={'percentage-display'}>70%</span> przy pobycie w szpitalu)</p>
                        </div>

                        <div className={'basic-holder-line'}>

                            <p className={'result-text'}>Za kady dzie nieobecnoci pracownik dostanie <span className={'money-display'}>{dzienNieobecnosciPracownik}z</span></p>

                        </div>
                    </div>

                    <div className={'compare-holder-b2b'}>
                        <h3>Dziaalno gospodarcza</h3>

                        <div className={' basic-holder'}>
                            <p className={'result-text'}>Podstawa</p>
                            <p className={'result-text basic-value-display'}><span className={'money-display'}>{podstawaB2B0}z</span></p>
                        </div>

                        <div className={'basic-holder-col'}>
                            <p className={'result-text'}><span className={'money-display'}>{podstawaB2B1}z</span> {'\x3C'}-- minus <span className={'percentage-display'}>13,71%</span></p>
                            <p className={'result-text'}><span className={'money-display'}>{podstawaB2B2}z</span> {'\x3C'}-- z tego <span className={'percentage-display'}>80%</span> (<span className={'percentage-display'}>70%</span> przy pobycie w szpitalu)</p>
                        </div>

                        <div className={'basic-holder-col'}>
                            <p className={'result-text-fit'}>Za kazdy dzie nieobecnoci przedsibiorca dostanie <span className={'money-display'}>{Math.round((podstawaB2B2 / 30) *100) / 100}zl</span></p>
                            <p className={'result-text-fit'}>Razem na dzie <span className={'money-display'}>{Math.round((dzienNieobecnosciPrzedsiebiorcy) *100) /100}zl</span></p>
                            <p className={'result-text-fit'}>W czasie choroby nie musi te paci skadek spoecznych czyli dziennie: <span className={'money-display'}>7.12zl</span></p>
                        </div>

                    </div>

                </div>
            </div>
        );
    }
}

export default connect(mapStateToProps)(Gibs);